import { Button, Form } from "react-bootstrap";
import CorrectAlternativeAnswer from "./CorrectAlternativeAnswer";
import IncorrectAlternativeAnswer from "./IncorrectAlternativeAnswer";
import { useEffect, useState } from "react";

export default function AlternativeContainer(props){

    function addIncorrect(){
        props.addIncorrect()
    }

    return(<>
        <CorrectAlternativeAnswer correctAnswer={props.correctAnswer}
                                handleCorrectAnswer={props.handleCorrectAnswer}
                                reorder={props.reorder}/>
        <Form.Group controlId="formBasicName">
            <Form.Label>Otras respuestas</Form.Label>
            {props.alternativeAnswers.map((alternative, index) => {
                const newAlternative = <IncorrectAlternativeAnswer 
                                        incorrectAnswer={alternative}
                                        index={index}
                                        handleIncorrectAnswer={props.handleIncorrectAnswer}
                                        reorder={props.reorder}/>
                return newAlternative
            })}
        </Form.Group>
        <div style={{"padding": "1vh"}}></div>
        <Button variant="secondary" onClick={addIncorrect} disabled={props.alternativeAnswers.length > 4}>Agregar Alternativa</Button>
    </>)
}