import { Form } from "react-bootstrap";
import { ChromePicker } from 'react-color';

export default function GroupForm(props){

    return(<>
        <Form.Group controlId="formBasicName">
        <Form.Label>Nombre</Form.Label>
        <Form.Control
            type="text"
            placeholder="Ingresa nombre del grupo"
            name="name"
            value={props.formData.name}
            onChange={props.handleInputChange}
        />
        </Form.Group>

        <Form.Group controlId="formBasicLogo">
        <Form.Label>Link de logo</Form.Label>
        <Form.Control
            type="text"
            placeholder="Ingresa link del logo"
            name="logo"
            value={props.formData.logo}
            onChange={props.handleInputChange}
        />
        </Form.Group>

        <Form.Group controlId="formBasicColor">
        <Form.Label>Color de Fondo</Form.Label>
        <br />
        <div style={{ display: 'flex', alignItems: 'center' }}>
            <Form.Control
            type="text"
            value={props.formData.backColor}
            readOnly
            style={{ width: '60px', marginRight: '10px' }}
            />
            <ChromePicker color={props.formData.backColor} onChange={props.handleBackColorChange} />
        </div>
        </Form.Group>

        <Form.Group controlId="formBasicColor">
        <Form.Label>Color de Texto</Form.Label>
        <br />
        <div style={{ display: 'flex', alignItems: 'center' }}>
            <Form.Control
            type="text"
            value={props.formData.txtColor}
            readOnly
            style={{ width: '60px', marginRight: '10px' }}
            />
            <ChromePicker color={props.formData.txtColor} onChange={props.handleTxtColorChange} />
        </div>
        </Form.Group>

    </>)
}