import { useEffect, useState } from "react";
import { Form } from "react-bootstrap";
import Datetime from 'react-datetime';
import 'react-datetime/css/react-datetime.css';

export default function EvaluationForm(props){

    const [groupData, setGroupData] = useState([])
    const [testData, setTestData] = useState([])

    function search(model){
        let url = process.env.REACT_APP_BACK_URL + "/" + model
        const uToken = localStorage.getItem("token")
        fetch(url, {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                'Authorization' : "Bearer "+ uToken
            }
        }).then(response => response.json())
        .then(data => {
            let modelData = data.data
            let modelArray = []
            modelData.forEach((modelIndex) => {
                let modelArrayIndex = {
                    "name": modelIndex.name,
                    "id": modelIndex.id
                }
                modelArray.push(modelArrayIndex)
            })
            if (model === "test") setTestData(modelArray);
            else setGroupData(modelArray)
        })
    }

    useEffect(() => {
        search("test")
        search("group")
    }, [])

    return(<>
        <Form.Group controlId="formBasicName">
        <Form.Label>Nombre</Form.Label>
        <Form.Control
            type="text"
            placeholder="Ingresa nombre de la evaluación"
            name="name"
            value={props.formData.name}
            onChange={props.handleInputChange}
        />
        </Form.Group>

        <Form.Group controlId="formBasicDesc">
        <Form.Label>Descripción</Form.Label>
        <Form.Control
            type="text"
            placeholder="Ingresa descripción de la evaluación"
            name="description"
            value={props.formData.description}
            onChange={props.handleInputChange}
        />
        </Form.Group>

        <Form.Group controlId="formBasicDateTime">
        <Form.Label>Tiempo de entrega</Form.Label>
        <br />
        <Datetime
            name="endTime"
            value={props.formData.endTime}
            onChange={props.handleDatetimeChange}
            dateFormat="DD/MM/YYYY"
            timeFormat="HH:mm"
            inputProps={{ placeholder: 'Selecciona fecha y hora' }}
        />
        </Form.Group>

        <Form.Group controlId="formBasicGroupDropdown">
          <Form.Label>Grupo</Form.Label>
          <Form.Select
            name="groupId"
            value={props.formData.groupId}
            onChange={props.handleInputChange}
          >
            <option value="">Seleccionar opción</option>
            {groupData.map((option) => (
              <option key={option.id} value={option.id}>
                {option.name}
              </option>
            ))}
          </Form.Select>
        </Form.Group>

        <Form.Group controlId="formBasicTestDropdown">
          <Form.Label>Test</Form.Label>
          <Form.Select
            name="testId"
            value={props.formData.testId}
            onChange={props.handleInputChange}
          >
            <option value="">Seleccionar opción</option>
            {testData.map((option) => (
              <option key={option.id} value={option.id}>
                {option.name}
              </option>
            ))}
          </Form.Select>
        </Form.Group>
    </>)
}