import { useEffect, useState } from "react"
import ContentTable from "../Common/Table"
import ViewHeader from "../Header/ViewHeader"
import Options from "../Common/Options"
import Valid from "../Common/Valid"
import CreateModal from "../Modals/CreateModal"
import UpdateModal from "../Modals/UpdateModal"
import DeleteModal from "../Modals/DeleteModal"
import { Spinner } from "react-bootstrap"

export default function User(props){

    const columns = [
        "Nombre", "Administrador", "Evaluador", "Visualizador", "Opciones"
    ]
    const [data, setData] = useState([])
    const [loaded, setLoaded] = useState(false)

    const [showCreate, setShowCreate] = useState(false)
    const [showUpdate, setShowUpdate] = useState(false)
    const [showDelete, setShowDelete] = useState(false)

    const [selectedId, setSelectedId] = useState('0')

    function showUpdateModal(selectId){
        setSelectedId(selectId)
        setShowUpdate(true)
    }

    function showDeleteModal(selectId){
        setSelectedId(selectId)
        setShowDelete(true)
    }

    function getAllUsers(){
        setData([])
        const uToken = localStorage.getItem("token")
        let dataUrl = `${process.env.REACT_APP_BACK_URL}/users`
        fetch(dataUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
        .then(response => response.json())
        .then(res => {
            let users = res.data
            let usersData = []
            users.forEach((uIndex) => {
                let userIndexData = [
                    uIndex.name,
                    <Valid valid={uIndex.admin}/>,
                    <Valid valid={uIndex.evaluator}/>,
                    <Valid valid={uIndex.visualizator}/>,
                    <Options id={uIndex.id} showUpdate={showUpdateModal} showDelete={showDeleteModal}/>
                ]
                usersData.push(userIndexData)
            })
            setData(usersData)
            setLoaded(true)
        })
    }

    useEffect(() => {
        getAllUsers()
    }, [])

    return(
        <>
            {loaded ? <>
                <CreateModal show={showCreate} model={"usuario"}
                         handleClose={() => {setShowCreate(false)}} submit={getAllUsers}/>
                {selectedId !== "0" ? 
                    <UpdateModal show={showUpdate} model={"usuario"} id={selectedId}
                        handleClose={() => {setSelectedId("0"); setShowUpdate(false)}} submit={getAllUsers} 
                        refresh={props.refresh}/> 
                : <></>}           
                <DeleteModal show={showDelete} model={"usuario"} id={selectedId}
                            handleClose={() => {setShowDelete(false)}} submit={getAllUsers}/>
                <ViewHeader content={"Usuarios"}  createMethod={() => {setShowCreate(true)}}/> 
                <ContentTable columns={columns} data={data}></ContentTable>  
            </> : <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><Spinner/></div>}
            
        </>
    )
}