import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import UserForm from '../Forms/UserForm';
import AlertMessage from '../Common/Alert';
import TestForm from '../Forms/TestForm';
import GroupForm from '../Forms/GroupForm';
import EvaluationForm from '../Forms/EvaluationForm';
import moment from 'moment';
import PersonForm from '../Forms/PersonForm';


export default function CreateModal(props){

    const [formData, setFormData] = useState(setForm());

    const [showAlert, setShowAlert] = useState(false)
    const [showMessage, setShowMessage] = useState("Error")

    const [enable, setEnabled] = useState(true)

    const handleInputChange = (event) => {
        const { name, value, type, checked } = event.target || event;

        if (type === 'checkbox') {
            setFormData({ ...formData, [name]: checked });
        } 
        else if (type === 'datetime') {
            const dateTimeValue = value ? new Date(value) : null;
            setFormData({ ...formData, [name]: dateTimeValue });
        }  
        else {
            setFormData({ ...formData, [name]: value });
        }
    };

    const handleDatetimeChange = (date) => {
        setFormData({ ...formData, endTime: date });
    };

    const handleBackColorChange = (color) => {
        setFormData({ ...formData, backColor: color.hex });
    };

    const handleTxtColorChange = (color) => {
        setFormData({ ...formData, txtColor: color.hex });
    };

    function setForm(){
        if (props.model === "usuario"){
            return {
                username: '',
                isAdmin: false,
                isEvaluator: false,
                isVisualizator: false,
                password: '',
                confirm: '',
            }
        }
        if (props.model === "evaluación"){
            return {
                name: '',
                description: '',
                endTime: null,
                groupId: '',
                testId: ''
            }
        }
        if (props.model === "prueba"){
            return {
                name: ''
            }
        }
        if (props.model === "grupo"){
            return {
                name: '',
                logo: '',
                backColor: '#ffffff',
                txtColor: '#ffffff'
            }
        }
        if (props.model === "persona"){
            return {
                firstName: "",
                lastName: "",
                email: ""
            }
        }
    }

    function submit(){
        setShowAlert(false)
        setEnabled(false)
        let url = process.env.REACT_APP_BACK_URL
        const uToken = localStorage.getItem("token")
        switch (props.model){
            case "usuario":
                url += "/users"
                let roles = []
                if (formData.isAdmin) roles.push(0);
                if (formData.isEvaluator) roles.push(1);
                if (formData.isVisualizator) roles.push(2);
                formData.roles = roles
                if (formData.confirm !== formData.password){
                    setShowMessage("Confirme correctamente la contraseña")
                    setShowAlert(true)
                    return
                }
                break
            case "evaluación":  
                let offset = -moment().tz("America/Santiago").utcOffset()/60
                formData.endTime = moment(formData.endTime).add(offset, "hours").format("MM/DD/YY HH:mm:ss")
                url += "/evaluation"
                break
            case "grupo":
                url += "/group"
                break
            case "prueba":
                url += "/test"
                break
            case "persona":
                url += `/group/${props.id}/person`
                break
        }
        fetch(url, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization' : "Bearer "+ uToken
            },
            body: JSON.stringify(formData)
        }).then(response => response.json())
        .then(data => {
            if (data.code !== 200){
                setShowMessage(data.message)
                setShowAlert(true)
            }
            else{
                props.submit()
                setFormData(setForm())
                props.handleClose()
            }
            setEnabled(true)
        }).catch(() => {
            setShowMessage("Ocurrio algo inesperado, intentalo de nuevo")
            setShowAlert(true)
            setEnabled(true)
        })   
    }

    function close(){
        setFormData(setForm())
        setShowAlert(false)
        props.handleClose()
    }

    return(
        <Modal show={props.show} onHide={close} id={`modal-${props.model.toLowerCase()}`}>
            <Modal.Header closeButton>
                <Modal.Title>Crear {props.model}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {props.model === "usuario" ? <UserForm formData={formData} handleInputChange={handleInputChange}/> : <></>}
                {props.model === "prueba" ? <TestForm formData={formData} handleInputChange={handleInputChange}/> : <></>}
                {props.model === "grupo" ? <GroupForm formData={formData} handleInputChange={handleInputChange}
                                            handleTxtColorChange={handleTxtColorChange}
                                            handleBackColorChange={handleBackColorChange}/> : <></>}
                {props.model === "evaluación" ? <EvaluationForm formData={formData} handleInputChange={handleInputChange}
                                                handleDatetimeChange={handleDatetimeChange}/> : <></>}
                {props.model === "persona" ? <PersonForm formData={formData} handleInputChange={handleInputChange}/> : <></>}                                
                {showAlert ? <AlertMessage alert="danger" message={showMessage}/> : <></>}  
            </Modal.Body>
            <Modal.Footer>
            <Button variant="secondary" onClick={close}>
                Cerrar
            </Button>
            <Button variant="success" onClick={submit} disabled={!enable}>
                Crear
            </Button>
            </Modal.Footer>
        </Modal>
    )
}