import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';


export default function NotifyModal(props){

    function submit(){
        let url = process.env.REACT_APP_BACK_URL + "/evaluation/send/" + props.id
        const uToken = localStorage.getItem("token")
        fetch(url, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization' : "Bearer "+ uToken
            }
        })
        props.submit()
        props.handleClose()
    }

    return(
        <Modal show={props.show} onHide={props.handleClose} id={`modal-${props.model.toLowerCase()}`}>
            <Modal.Header closeButton>
                <Modal.Title>Notificar {props.model}</Modal.Title>
            </Modal.Header>
            <Modal.Body>¿Desea notificar a los miembros del grupo para que comienzen la evaluación?</Modal.Body>
            <Modal.Footer>
            <Button variant="secondary" onClick={props.handleClose}>
                Cerrar
            </Button>
            <Button variant="primary" onClick={submit}>
                Notificar
            </Button>
            </Modal.Footer>
        </Modal>
    )
}