import "../../css/content.css"
import { Button } from "react-bootstrap"
import Switch from "../Common/Switch"

export default function ViewHeader(props){

    return(
        <div className="header">
            {props.detail ? 
            <div className="header-title">{props.name}</div>
            :
            <div className="header-title">ADMINISTRACIÓN DE {props.content.toUpperCase()}</div>}
            
            <Button variant="success" className="header-button"
                onClick={props.createMethod} disabled={props.disabled}>{props.detail ? "Agregar" : "Crear" } {props.content}
            </Button>
            {props.content === "Pregunta" ? <>
            <div className="header-opt">Ordenar Preguntas</div>
            <div className="header-switch"> 
                <Switch isOn={props.reorder} handleToggle={props.handleReorder}/>
            </div>
            </> : <></>}
        </div>          
    )
}