import "../../css/spinner.css";

export default function LoadingSpinner(props) {
  return (
    <>
    {props.small ? 
      <div className="spinner-container-small">
        <div className="loading-spinner-small">
        </div>
      </div>
      :
        <div className="spinner-container">
          <div className="loading-spinner">
          </div>
        </div>
    }
    </>
    
    
  );
}