import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import AlertMessage from '../Common/Alert';
import { useState } from 'react';


export default function DeleteModal(props){

    const [showAlert, setShowAlert] = useState(false)

    function submit(){
        let url = process.env.REACT_APP_BACK_URL
        const uToken = localStorage.getItem("token")
        switch (props.model){
            case "usuario":
                url += "/users"
                break
            case "evaluación":
                url += "/evaluation"
                break
            case "grupo":
                url += "/group"
                break
            case "prueba":
                url += "/test"
                break
            case "persona":
                url += "/person"
                break
        }
        url += `/${props.id}`
        fetch(url, {
            method: "DELETE",
            headers: {
                "Content-Type": "application/json",
                'Authorization' : "Bearer "+ uToken
            }
        }).then(response => response.json())
        .then(data => {
            if (data.code === 200){
                props.handleClose()
                props.submit()
                setShowAlert(false)
            }
            else{
                setShowAlert(true)
            }
        })      
    }

    function close(){
        setShowAlert(false)
        props.handleClose()
    }

    return(
        <Modal show={props.show} onHide={close} id={`modal-${props.model.toLowerCase()}`}>
            <Modal.Header closeButton>
                <Modal.Title>Eliminar {props.model}</Modal.Title>
            </Modal.Header>
            <Modal.Body>¿Desea eliminar el {props.model}?</Modal.Body>
            {showAlert ? <AlertMessage alert="danger" message={"Este " + props.model.toLowerCase() + " no puede ser eliminado"}/> : <></>}  
            <Modal.Footer>
            <Button variant="secondary" onClick={close}>
                Cerrar
            </Button>
            <Button variant="danger" onClick={submit}>
                Eliminar
            </Button>
            </Modal.Footer>
        </Modal>
    )
}