import "../../css/home.css"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {faUser, faPenToSquare, faQuestion, faUserGroup, faRightFromBracket} from "@fortawesome/free-solid-svg-icons";
import { useState } from "react";
import User from "../Views/User";
import Evaluation from "../Views/Evaluation";
import Test from "../Views/Test";
import Group from "../Views/Group";
import Person from "../Views/Person";
import Question from "../Views/Question";

export default function Home(props){

    const [selected, setSelected] = useState(setDefaultSelected())

    const [objId, setObjId] = useState("0")

    function setDefaultSelected(){
        if (props.isAdmin)
            return 0
        return 1
    }

    return (
        <div className="basecontainer">
            <div className="containerNavbar">
                {props.isAdmin ? 
                    <FontAwesomeIcon icon={faUser} className={selected === 0 ? "icon-selected" : "icon"} onClick={() => {setSelected(0)}}/> 
                : <></>}
                {props.isEvaluator ? 
                <>
                    <FontAwesomeIcon icon={faPenToSquare} className={selected === 1 ? "icon-selected" : "icon"} onClick={() => {setSelected(1)}}/>
                    <FontAwesomeIcon icon={faQuestion} className={selected === 2 || selected === 4 ? "icon-selected" : "icon"} onClick={() => {setSelected(2)}}/>
                    <FontAwesomeIcon icon={faUserGroup} className={selected === 3 || selected === 5 ? "icon-selected" : "icon"} onClick={() => {setSelected(3)}}/>
                </>
                :<></>} 
                <div className="hello">HOLA {props.name}</div> 
                <FontAwesomeIcon icon={faRightFromBracket} className="danger-icon" onClick={() => {localStorage.clear(); props.close()}}/>
                      
            </div>
            <div className="containerHome">
                {selected === 0 ? <div className="containerTitle"><User refresh={props.refresh}/></div>:<></>}
                {selected === 1 ? <div className="containerTitle"><Evaluation/></div>:<></>}
                {selected === 2 ? <div className="containerTitle"><Test goto={() => {setSelected(4)}}
                                                                    select={setObjId}/></div>:<></>}
                {selected === 3 ? <div className="containerTitle"><Group goto={() => {setSelected(5)}} 
                                                                    select={setObjId}/></div>:<></>}
                {selected === 4 ? <div className="containerTitle"><Question id={objId}/></div>:<></>}
                {selected === 5 ? <div className="containerTitle"><Person id={objId}/></div>:<></>}
            </div>
        </div>
    )
}