import { useEffect, useState } from "react";
import ViewHeader from "../Header/ViewHeader";
import ContentTable from "../Common/Table";
import Options from "../Common/Options";
import CreateModal from "../Modals/CreateModal";
import UpdateModal from "../Modals/UpdateModal";
import DeleteModal from "../Modals/DeleteModal";
import { Spinner } from "react-bootstrap";

export default function Test(props){

    const columns = [
        "Nombre", "Cantidad de preguntas", "Opciones"
    ]
    const [data, setData] = useState([])

    const [showCreate, setShowCreate] = useState(false)
    const [showUpdate, setShowUpdate] = useState(false)
    const [showDelete, setShowDelete] = useState(false)

    const [selectedId, setSelectedId] = useState('0')
    const [loaded, setLoaded] = useState(false)

    function showUpdateModal(selectId){
        setSelectedId(selectId)
        setShowUpdate(true)
    }

    function showDeleteModal(selectId){
        setSelectedId(selectId)
        setShowDelete(true)
    }

    function getAllTests(){
        setData([])
        const uToken = localStorage.getItem("token")
        let dataUrl = `${process.env.REACT_APP_BACK_URL}/test`
        fetch(dataUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
        .then(response => response.json())
        .then(res => {
            let questions = res.data
            let questionData = []
            questions.forEach((qIndex) => {
                let questionIndexData = [
                    qIndex.name,
                    qIndex.questionQuantity,
                    <Options detail={true} id={qIndex.id} showUpdate={showUpdateModal} showDelete={showDeleteModal}
                            goto={() => {props.select(qIndex.id); props.goto()}}/>
                ]
                questionData.push(questionIndexData)
            })
            setData(questionData)
            setLoaded(true)
        })
    }

    useEffect(() => {
        getAllTests()
    }, [])

    return(
        <>
            {loaded ? <>
                <CreateModal show={showCreate} model={"prueba"}
                         handleClose={() => {setShowCreate(false)}} submit={getAllTests}/>
                {selectedId !== "0" ? 
                    <UpdateModal show={showUpdate} model={"prueba"} id={selectedId}
                        handleClose={() => {setSelectedId("0"); setShowUpdate(false)}} submit={getAllTests}/> 
                : <></>}  
                <DeleteModal show={showDelete} model={"prueba"} id={selectedId}
                            handleClose={() => {setShowDelete(false)}} submit={getAllTests}/>
                <ViewHeader content={"Pruebas"} createMethod={() => {setShowCreate(true)}}/> 
                <ContentTable columns={columns} data={data}></ContentTable>  
            </>:<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><Spinner/></div>}
            
        </>
    )
}