import '../../css/login.css';
import { useState } from 'react';
import { Form, FormControl, Button } from 'react-bootstrap';
import AlertMessage from '../Common/Alert';

export default function Login(props){

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const [isAuth, setAuth] = useState(false);
    const [showAlert, setShowAlert] = useState(false)

    const handleSubmit = async (event) => {
        event.preventDefault();
        setAuth(true)
        setShowAlert(false)
        let loginURL = `${process.env.REACT_APP_BACK_URL}/login`
        let body = {
            "username": username,
            "password": password
        }
        await fetch(loginURL, {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
        }).then(response => response.json())
        .then(async data => {
            if (data.code !== 200){
                setShowAlert(true)
            }
            else{
                localStorage.setItem('token', data.token);
                props.authenticate()
            }
        })
        .catch(error => console.error(error));
        setAuth(false)

    }

    const handleUsernameChange = (event) => {
        setUsername(event.target.value);
    }

    const handlePasswordChange = (event) => {
        setPassword(event.target.value);
    }

    return(
        <div className='basecontainer'>
            <div className='containerlogin'>          
                <Form onSubmit={handleSubmit}>
                    <h2 className='label'>INICIAR SESIÓN</h2>
                    <div className='space'></div>
                    <Form.Group controlId="formBasicEmail" className='inputstyle'>
                        <Form.Label>Usuario</Form.Label>
                        <FormControl type="text" placeholder="Ingrese su usuario" value={username} onChange={handleUsernameChange}/>
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword" className='inputstyle'>
                        <Form.Label>Contraseña</Form.Label>
                        <FormControl type="password" placeholder="Ingrese su contraseña" value={password} onChange={handlePasswordChange}/>
                    </Form.Group>
                    <div className='space'></div>
                    <div className='buttoncenter'>
                        <Button variant="primary" type="submit" 
                            className='buttonsize' disabled={isAuth || !username || !password}>
                                Iniciar sesión
                        </Button>
                    </div>  
                    {showAlert || !props.authorized ? 
                        <>
                            <div className='space'></div>
                            <AlertMessage alert={"danger"} message={"Credenciales Inválidas"} />
                        </>          
                    :
                    <p></p>}
                </Form>
            </div>
        </div>
    );
}