import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import UserForm from '../Forms/UserForm';
import TestForm from '../Forms/TestForm';
import GroupForm from '../Forms/GroupForm';
import EvaluationForm from '../Forms/EvaluationForm';
import AlertMessage from '../Common/Alert';
import moment from 'moment';
import 'moment-timezone';
import PersonForm from '../Forms/PersonForm';


export default function UpdateModal(props){

    const [infoLoaded, setInfoLoaded] = useState(false);
    const [formData, setFormData] = useState({});

    const [showAlert, setShowAlert] = useState(false)
    const [showMessage, setShowMessage] = useState("Error")

    const handleInputChange = (event) => {
        const { name, value, type, checked } = event.target || event;

        if (type === 'checkbox') {
            setFormData({ ...formData, [name]: checked });
        } 
        else if (type === 'datetime') {
            const dateTimeValue = value ? new Date(value) : null;
            setFormData({ ...formData, [name]: dateTimeValue });
        }  
        else if (type === 'color') {
            setFormData({ ...formData, [name]: value });
        }
        else {
            setFormData({ ...formData, [name]: value });
        }
    };

    const handleBackColorChange = (color) => {
        setFormData({ ...formData, backColor: color.hex });
    };

    const handleTxtColorChange = (color) => {
        setFormData({ ...formData, txtColor: color.hex });
    };

    const handleDatetimeChange = (date) => {
        setFormData({ ...formData, endTime: date });
    };

    function setForm(){
        let url = process.env.REACT_APP_BACK_URL
        const uToken = localStorage.getItem("token")
        switch (props.model){
            case "usuario":
                url += "/users"
                break
            case "evaluación":
                url += "/evaluation/fk"
                break
            case "grupo":
                url += "/group"
                break
            case "prueba":
                url += "/test"
                break
            case "persona":
                url += "/person"
                break
        }
        url += `/${props.id}`
        fetch(url, {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                'Authorization' : "Bearer "+ uToken
            }
        }).then(response => response.json())
        .then(data => {
            if (props.model === "usuario"){
                setFormData({
                    username: data.data.username,
                    isAdmin: data.data.roles.includes(0),
                    isEvaluator: data.data.roles.includes(1),
                    isVisualizator: data.data.roles.includes(2),
                    password: '',
                    confirm: '',
                })
            }
            if (props.model === "evaluación"){
                setFormData({
                    name: data.data.name,
                    description: data.data.description,
                    endTime: moment(data.data.endTime).format("MM/DD/YY HH:mm:ss"),
                    groupId: data.data.groupId,
                    testId: data.data.testId
                })
            }
            if (props.model === "prueba"){
                setFormData({
                    name: data.data.name
                })
            }
            if (props.model === "grupo"){
                setFormData({
                    name: data.data.name,
                    logo: data.data.logo,
                    backColor: data.data.backColor,
                    txtColor: data.data.txtColor
                })
            }
            if (props.model === "persona"){
                setFormData({
                    firstName: data.data.firstName,
                    lastName: data.data.lastName,
                    email: data.data.email
                })
            }
            setInfoLoaded(true)
        })
    }

    function submit(){
        setShowAlert(false)
        let url = process.env.REACT_APP_BACK_URL
        const uToken = localStorage.getItem("token")
        switch (props.model){
            case "usuario":
                url += "/users"
                let roles = []
                if (formData.isAdmin) roles.push(0);
                if (formData.isEvaluator) roles.push(1);
                if (formData.isVisualizator) roles.push(2);
                formData.roles = roles
                if (formData.confirm !== formData.password){
                    setShowMessage("Confirme correctamente la contraseña")
                    setShowAlert(true)
                    return
                }
                if (formData.password === ''){
                    delete formData.password
                    delete formData.confirm
                }                 
                break
            case "evaluación":
                let offset = -moment().tz("America/Santiago").utcOffset()/60
                formData.endTime = moment(formData.endTime).add(offset, "hours").format("MM/DD/YY HH:mm:ss")
                url += "/evaluation"
                break
            case "grupo":
                url += "/group"
                break
            case "prueba":
                url += "/test"
                break
            case "persona":
                url += "/person"
                break
        }
        url += `/${props.id}`
        fetch(url, {
            method: "PATCH",
            headers: {
                "Content-Type": "application/json",
                'Authorization' : "Bearer "+ uToken
            },
            body: JSON.stringify(formData)
        }).then(response => response.json())
        .then(data => {
            if (data.code !== 200){
                setShowMessage(data.message)
                formData.password = ""
                formData.confirm = ""
                setShowAlert(true)
            }
            else{
                props.submit()
                setFormData(setForm())
                props.handleClose()
                let userId = localStorage.getItem("id")
                if (userId === props.id){
                    props.refresh()
                }
            }
        }).catch((error) => {
            setShowMessage("Ocurrio algo inesperado, intentalo de nuevo")
            setShowAlert(true)
        })   
    }

    function close(){
        setShowAlert(false)
        props.handleClose()
    }

    useState(() => {
        setForm()
    }, [])

    return(
        <Modal show={props.show} onHide={props.handleClose} id={`modal-${props.model.toLowerCase()}`}>
            <Modal.Header closeButton>
                <Modal.Title>Actualizar {props.model}</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {infoLoaded ? <>
                    {props.model === "usuario" ? <UserForm formData={formData} handleInputChange={handleInputChange}/> : <></>}
                    {props.model === "prueba" ? <TestForm formData={formData} handleInputChange={handleInputChange}/> : <></>}
                    {props.model === "grupo" ? <GroupForm formData={formData} handleInputChange={handleInputChange}
                                                handleTxtColorChange={handleTxtColorChange}
                                                handleBackColorChange={handleBackColorChange}/> : <></>}
                    {props.model === "evaluación" ? <EvaluationForm formData={formData} handleInputChange={handleInputChange}
                                                    handleDatetimeChange={handleDatetimeChange}/> : <></>}
                    {props.model === "persona" ? <PersonForm formData={formData} handleInputChange={handleInputChange}/> : <></>}  
                    {showAlert ? <AlertMessage alert="danger" message={showMessage}/> : <></>}  
                </>: <></>}                
            </Modal.Body>
            <Modal.Footer>
            <Button variant="secondary" onClick={close}>
                Cerrar
            </Button>
            <Button variant="primary" onClick={submit}>
                Actualizar
            </Button>
            </Modal.Footer>
        </Modal>
    )
}