import { useEffect, useState } from "react";
import ContentTable from "../Common/Table";
import ViewHeader from "../Header/ViewHeader";
import { Spinner } from "react-bootstrap";
import Options from "../Common/Options";
import CreateModal from "../Modals/CreateModal";
import UpdateModal from "../Modals/UpdateModal";
import DeleteModal from "../Modals/DeleteModal";

export default function Person(props){

    const columns = [
        "Nombre", "Apellido", "Correo", "Opciones"
    ]
    const [data, setData] = useState([])

    const [name, setName] = useState("")
    const [loaded, setLoaded] = useState(false)

    const [showCreate, setShowCreate] = useState(false)
    const [showUpdate, setShowUpdate] = useState(false)
    const [showDelete, setShowDelete] = useState(false)

    const [selectedId, setSelectedId] = useState('0')

    function showUpdateModal(selectId){
        setSelectedId(selectId)
        setShowUpdate(true)
    }

    function showDeleteModal(selectId){
        setSelectedId(selectId)
        setShowDelete(true)
    }

    function getInfoFromGroup(){
        setData([])
        const uToken = localStorage.getItem("token")
        let dataUrl = `${process.env.REACT_APP_BACK_URL}/group/${props.id}`
        fetch(dataUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
        .then(response => response.json())
        .then(res => {
            let groups = res.data
            setName(groups.name)
            let groupData = []
            groups.persons.forEach((pIndex) => {
                let groupIndexData = [
                    pIndex.firstName,
                    pIndex.lastName,
                    pIndex.email,
                    <Options id={pIndex.id} showUpdate={showUpdateModal} showDelete={showDeleteModal}/>
                ]
                groupData.push(groupIndexData)
            })
            setData(groupData)
            setLoaded(true)
        })
    }

    useEffect(() => {
        getInfoFromGroup()
    }, [])

    return(
        <>
            {loaded ? <>
            <CreateModal show={showCreate} model={"persona"} id={props.id}
                        handleClose={() => {setShowCreate(false)}} submit={getInfoFromGroup}/>
            {selectedId !== "0" ? 
                <UpdateModal show={showUpdate} model={"persona"} id={selectedId}
                    handleClose={() => {setSelectedId("0"); setShowUpdate(false)}} submit={getInfoFromGroup}/> 
            : <></>}           
            <DeleteModal show={showDelete} model={"persona"} id={selectedId}
                        handleClose={() => {setShowDelete(false)}} submit={getInfoFromGroup}/>
            <ViewHeader detail={true} name={name} content={"Persona"} createMethod={() => {setShowCreate(true)}}/> 
            <ContentTable columns={columns} data={data}></ContentTable>  
            </>:<div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><Spinner/></div>}
        </>
    )
}