import { Button } from "react-bootstrap"
import "../../css/content.css"

export default function Options(props){

    return(<>
        {props.detail ? <Button variant="primary" className="button-space" onClick={() => {props.goto()}}>Detalles</Button> : <></>}  
        {props.send ? <Button variant="primary" className="button-space" onClick={() => props.showNotify(props.id)}>Notificar</Button> : <></>}   
        <Button variant="primary" className="button-space" onClick={() => props.showUpdate(props.id)}>Actualizar</Button>
        <Button variant="danger"  className="button-space" onClick={() => props.showDelete(props.id)}>Eliminar</Button>
    </>)
}