import { useEffect, useState } from "react";
import ViewHeader from "../Header/ViewHeader";
import ContentTable from "../Common/Table";
import Options from "../Common/Options";
import CreateModal from "../Modals/CreateModal";
import UpdateModal from "../Modals/UpdateModal";
import DeleteModal from "../Modals/DeleteModal";
import { Spinner } from "react-bootstrap";

export default function Group(props){

    const columns = [
        "Nombre", "Cantidad de personas", "Opciones"
    ]
    const [data, setData] = useState([])

    const [showCreate, setShowCreate] = useState(false)
    const [showUpdate, setShowUpdate] = useState(false)
    const [showDelete, setShowDelete] = useState(false)

    const [selectedId, setSelectedId] = useState('0')
    const [loaded, setLoaded] = useState(false)

    function showUpdateModal(selectId){
        setSelectedId(selectId)
        setShowUpdate(true)
    }

    function showDeleteModal(selectId){
        setSelectedId(selectId)
        setShowDelete(true)
    }

    function getAllGroups(){
        setData([])
        const uToken = localStorage.getItem("token")
        let dataUrl = `${process.env.REACT_APP_BACK_URL}/group`
        fetch(dataUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
        .then(response => response.json())
        .then(res => {
            let groups = res.data
            let groupData = []
            groups.forEach((gIndex) => {
                let groupIndexData = [
                    gIndex.name,
                    gIndex.personQuantity,
                    <Options detail={true} id={gIndex.id} showUpdate={showUpdateModal} showDelete={showDeleteModal}
                        goto={() => {props.select(gIndex.id); props.goto()}}/>
                ]
                groupData.push(groupIndexData)
            })
            setData(groupData)
            setLoaded(true)
        })
    }

    useEffect(() => {
        getAllGroups()
    }, [])

    return(
        <>
            {loaded ? <>
                <CreateModal show={showCreate} model={"grupo"}
                         handleClose={() => {setShowCreate(false)}} submit={getAllGroups}/>
                {selectedId !== "0" ? 
                    <UpdateModal show={showUpdate} model={"grupo"} id={selectedId}
                        handleClose={() => {setSelectedId("0"); setShowUpdate(false)}} submit={getAllGroups}/> 
                : <></>}  
                <DeleteModal show={showDelete} model={"grupo"} id={selectedId}
                            handleClose={() => {setShowDelete(false)}} submit={getAllGroups}/>
                <ViewHeader content={"Grupos"} createMethod={() => {setShowCreate(true)}}/> 
                <ContentTable columns={columns} data={data}></ContentTable>  
            </> : <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><Spinner/></div>}
            
        </>
    )
}