import { useState } from "react";
import { Form } from "react-bootstrap";

export default function IncorrectAlternativeAnswer(props){

    const [inputAlternative, setInputAlternative] = useState(props.incorrectAnswer)

    function handleIncorrectAnswer(event){
        let value = event.target.value
        props.handleIncorrectAnswer(props.index, value)
        setInputAlternative(value)
    }
    
    return ( <>
        <Form.Control
            type="text"
            placeholder="Ingresa respuesta alternativa"
            name="incorrectAnswer"
            disabled={props.reorder}
            value={inputAlternative}
            onChange={handleIncorrectAnswer}
        />
    </>)
}