import { useEffect, useState } from "react"
import { Form } from "react-bootstrap"
import TrueFalseResponse from "./TrueOrFalseCotainer"
import AlternativeContainer from "./AlternativeContainer"
import Cross from "./Cross"

import { useSortable } from "@dnd-kit/sortable";
import {CSS} from "@dnd-kit/utilities";

export default function QuestionContainer(props){

    const [question, setQuestion] = useState("")
    const [type, setType] = useState("1")
    const [difficulty, setDifficulty] = useState("1")
    const [tags, setTags] = useState("")
    const [answerTrueOrFalse, setAnswerTrueOrFalse] = useState("true")
    const [correctAnswerAlternative, setCorrectAnswerAlternative] = useState("")
    const [incorrectAlternativeAnswers, setIncorrectAlternativesAnswers] = useState(["", ""])
    
    const [idSelected, setIdSelected] = useState("0")
    const [removed, setRemoved] = useState(false)

    const [loaded, setLoaded] = useState(false)

    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition
    } = useSortable({id: props.id});

    const style = {
        transform: CSS.Transform.toString(transform),
        transition
    }

    const handleQuestionChange = (event) => {
        setQuestion(event.target.value);
    }

    const handleTypeChange = (event) => {
        setType(event.target.value);
    }

    const handleRadioChange = (event) => {
        setAnswerTrueOrFalse(event.target.value)
    }

    const handleDifficultyChange = (event) => {
        setDifficulty(event.target.value)
    }

    const handleTagsChange = (event) => {
        setTags(event.target.value)
    }

    const handleCorrectAnswer = (event) => {
        setCorrectAnswerAlternative(event.target.value)
    }

    const addIncorrectAnswer = () => {
        setIncorrectAlternativesAnswers([...incorrectAlternativeAnswers, ""])
    }

    function setIncorrectAnswers(prev, index, value){
        const newValues = [...prev];
        newValues[index] = value;
        return newValues
    }

    function handleIncorrectAnswer(index, value){
        setIncorrectAlternativesAnswers((prev) => setIncorrectAnswers(prev, index, value))
    }

    useEffect(() => {
        let data = {
            "question": question,
            "category": parseInt(type),
            "difficulty": parseInt(difficulty),
            "tags": tags.split(";"),
            "answerTrueOrFalse": answerTrueOrFalse === "true",
            "correctAnswerAlternative": correctAnswerAlternative,
            "incorrectAnswerAlternative": incorrectAlternativeAnswers,
            "id": idSelected,
            "removed": removed,
            "index": props.index
        }
        props.updateData(props.index, data)
    }, [question, type, difficulty, tags, answerTrueOrFalse, correctAnswerAlternative, incorrectAlternativeAnswers, removed])
    
    
    useEffect(() => {
        if (props.data){
            setIdSelected(props.data.id)
            setQuestion(props.data.question)
            setType(props.data.category.toString())
            setDifficulty(props.data.difficulty.toString())
            setTags(props.data.tags.join(";"))
            setAnswerTrueOrFalse(props.data.answer.toString())
            if (props.data.id !== "0")
                setCorrectAnswerAlternative(props.data.answer) 
            if(props.data.options){
                setIncorrectAlternativesAnswers([...props.data.options.slice(0, props.data.options.length - 1)])   
            }    
            setRemoved(props.data.removed)       
        }
        setLoaded(true)
        
    }, [])

    let body =
        <div className="questionSubcontainer" id={props.number} key={props.key}>
            <Cross remove={setRemoved}/>
            <Form.Group controlId="formBasicName">
                <Form.Label>Pregunta {props.number + 1}</Form.Label>
                <Form.Control
                    type="text"
                    placeholder="Ingresa pregunta a realizar"
                    name="question"
                    value={question}
                    disabled={props.reorder}
                    onChange={handleQuestionChange}
                />
            </Form.Group>
            <div style={{"padding": "0.5vh"}}></div>
            <Form.Group controlId="formBasicDifficultyDropdown">
            <Form.Select
                name="groupId"
                value={difficulty}
                onChange={handleDifficultyChange}
                disabled={props.reorder}
            >
                <option value="1">Fácil</option>
                <option value="2">Normal</option>
                <option value="3">Dificil</option>
            </Form.Select>
            </Form.Group>
            <div style={{"padding": "0.5vh"}}></div>
            <Form.Group controlId="formBasicName">
                <Form.Control
                    type="text"
                    placeholder="Ingresa tags de la pregunta separadas usando ;"
                    name="question"
                    value={tags}
                    disabled={props.reorder}
                    onChange={handleTagsChange}
                />
            </Form.Group>
            {props.data.id === "0" ? 
            <>
                <div style={{"padding": "0.5vh"}}></div>
                <Form.Group controlId="formBasicTypeDropdown">
                <Form.Select
                    name="groupId"
                    value={type}
                    onChange={handleTypeChange}
                    disabled={props.reorder}
                >
                    <option value="1">Verdadero y Falso</option>
                    <option value="2">Alternativas</option>
                </Form.Select>
                </Form.Group>
            </>
            :
            <>
            </>}     
            {loaded ? <>
                <div style={{"padding": "1vh"}}></div>
                {type === "1" ? <TrueFalseResponse selected={answerTrueOrFalse} 
                                handleRadioOptionChange={handleRadioChange}
                                number={props.number} reorder={props.reorder}/> : <></>}
                {type === "2" ? <AlternativeContainer correctAnswer={correctAnswerAlternative}
                                handleCorrectAnswer={handleCorrectAnswer}
                                alternativeAnswers={incorrectAlternativeAnswers}
                                handleIncorrectAnswer={handleIncorrectAnswer}
                                addIncorrect={addIncorrectAnswer}
                                default={props.data && props.data.options ? props.data.options.length - 1 : false}
                                reorder={props.reorder}/> : <></>}
            </> : <></>}   
        </div>
        
    return (
        <>
            {removed ? <></> : 
            <>
                {props.reorder ?
                <div ref={setNodeRef} style={style} {...attributes} {...listeners}>
                    {body}
                </div>
                 :<>{body}</>}       
            </>}
            
        </>
    )
}