import { Form } from "react-bootstrap";

export default function CorrectAlternativeAnswer(props){
    return ( <>
        <Form.Group controlId="formBasicName">
            <Form.Label>Respuesta Correcta</Form.Label>
            <Form.Control
                type="text"
                placeholder="Ingresa respuesta correcta"
                name="correctAnswer"
                disabled={props.reorder}
                value={props.correctAnswer}
                onChange={props.handleCorrectAnswer}
            />
        </Form.Group>
    </>)
}