import { useEffect, useState } from "react";
import Header from "./components/Header/Header";
import Login from "./components/Login/Login";
import Home from "./components/Home/Home";
import LoadingSpinner from "./components/Common/Loading";

function App() {

  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [isAdmin, setIsAdmin] = useState(true)
  const [isEvaluator, setIsEvaluator] = useState(true)
  const [name, setName] = useState("ADMIN")

  const [authorized, setIsAuthorized] = useState(true)

  const [isLoaded, setLoaded] = useState(false)

  function readToken(){
    setIsAuthorized(true)
    setLoaded(false)
    const uToken = localStorage.getItem("token")
    let datesUrl = `${process.env.REACT_APP_BACK_URL}/me`
      fetch(datesUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
      .then(response => response.json())
      .then(res => {
          if (res.data === null){
            /*
            let refreshUrl = `${process.env.REACT_APP_BACK_URL}/refresh?rtoken=${localStorage.getItem("rtoken")}`
            fetch(refreshUrl, {method: "GET"}).then(response => response.json()).then((res => {
              if (res.data === null){
                window.location.href = process.env.REACT_APP_AUTH_FRONT
              }
              else{
                localStorage.setItem("token", res.data.token)
                localStorage.setItem("rtoken", res.data.rtoken)
                localStorage.setItem("user", res.data.data.firstName)
                localStorage.setItem("admin", res.data.data.isAdmin)
                setIsAdmin(res.data.data.isAdmin)
                setUserData(res.data.data)
                setIsAuthenticated(true)               
              }
            })) 
            */
            setIsAuthenticated(false)             
          }
          else{
              localStorage.setItem("id", res.data.id)
              setName(res.data.username)
              if(res.data.roles.includes(0) || res.data.roles.includes(1)){
                setIsAdmin(res.data.roles.includes(0))
                setIsEvaluator(res.data.roles.includes(1))
                setIsAuthenticated(true)
              }
              else{
                localStorage.clear()
                setIsAuthorized(false)
                setIsAuthenticated(false)
              }
          }
          setLoaded(true)
      })
  }

  useEffect(() => {readToken()}, [])

  return (
    <>
      <Header/>
      {isLoaded ? 
        <>
          {isAuthenticated ? 
            <Home isEvaluator={isEvaluator} isAdmin={isAdmin} close={() => {setIsAuthenticated(false)}} 
            name={name} refresh={readToken}/> 
            : 
            <Login authenticate={readToken} authorized={authorized}/>}
        </>
      : <>
        <LoadingSpinner/>
      </>} 
    </>
  );
}

export default App;
