import { useEffect, useState } from "react"
import ViewHeader from "../Header/ViewHeader"
import "../../css/question.css"
import QuestionContainer from "../Trivia/QuestionContainer"
import { Button } from "react-bootstrap"

import {
    DndContext,
    closestCenter
  } from "@dnd-kit/core";
  import {
    arrayMove,
    SortableContext,
    verticalListSortingStrategy
  } from "@dnd-kit/sortable";

export default function Question(props){

    const [name, setName] = useState("")
    const [loaded, setLoaded] = useState(false)

    const [questionsData, setQuestionsData] = useState([])
    const [enabled, setEnabled] = useState(true)

    const [reorder, setReorder] = useState(false)

    const uToken = localStorage.getItem("token")

    function getInfoFromTest(){
        setQuestionsData([])       
        let dataUrl = `${process.env.REACT_APP_BACK_URL}/test/${props.id}/opt`
        fetch(dataUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
        .then(response => response.json())
        .then(res => {
            let tests = res.data
            setName(tests.name)
            let questionsData = tests.questions
            let objectData = []
            for(let tIndex = 0; tIndex < questionsData.length; tIndex++){
                questionsData[tIndex].index = tIndex
                let qIndex = questionsData[tIndex]             
                objectData.push({
                    "question": qIndex.question,
                    "category": qIndex.category,
                    "difficulty": qIndex.difficulty,
                    "tags": qIndex.tags,
                    "answer": qIndex.answer,
                    "options": qIndex.options,
                    "id": qIndex.id,
                    "index": qIndex.index,
                    "removed": false
                })
            }
            setQuestionsData(objectData)
            setLoaded(true)
            setEnabled(true)
        })
    }

    useEffect(() => {
        getInfoFromTest()
    }, [])

    function changeMode(){
        setReorder(!reorder)
    }

    const addQuestion = () => {
        let questionDataIndex = {
            "question": "",
            "category": 1,
            "difficulty": 1,
            "tags": [],
            "answer": true,
            "id": "0",
            "index": questionsData.length,
            "removed": false,
            "options": ["", ""]
        }
        setQuestionsData([...questionsData, questionDataIndex])
    };

    function setQuestionIndexData(prev, index, data){
        const newValues = [...prev];
        newValues[index] = data;
        return newValues
    }

    function changeQuestionData(index, data){
        let options = []
        for(let iIndex = 0; iIndex < data.incorrectAnswerAlternative.length ; iIndex++){
            options.push(data.incorrectAnswerAlternative[iIndex])
        }
        options.push(data.correctAnswerAlternative)
        if(data.category === 1){
            data.answer = data.answerTrueOrFalse
        }
        else{
            data.answer = data.correctAnswerAlternative
            data.options = options
        }
        delete data.answerTrueOrFalse
        delete data.incorrectAnswerAlternative
        delete data.correctAnswerAlternative
        setQuestionsData((prev) => setQuestionIndexData(prev, index, data))
    }

    async function saveChanges(){
        setEnabled(false)
        let url = `${process.env.REACT_APP_BACK_URL}/test/${props.id}/question`
        let order = 1
        for(let qIndex = 0; qIndex < questionsData.length; qIndex++){
            let qData = questionsData[qIndex]
            let updateURL = `${process.env.REACT_APP_BACK_URL}/question/${qData.id}`
            if(qData.id === "0"){
                if(!qData.removed){
                    qData.order = order
                    await fetch(url, {method: "POST", 
                            headers: {'Authorization' : "Bearer "+ uToken, 
                                        "Content-Type": "application/json",}, 
                            body: JSON.stringify(qData)})
                    .then(response => response.json())
                    order++
                }     
            }
            else{
                let methodAPI = "DELETE"
                if(!qData.removed){
                    methodAPI = "PATCH"
                    qData.order = order
                    order++
                }
                await fetch(updateURL, {method: methodAPI, 
                    headers: {'Authorization' : "Bearer "+ uToken, 
                                "Content-Type": "application/json",}, 
                    body: JSON.stringify(qData)})
                .then(response => response.json())
            }
        }
        getInfoFromTest()
    }   

    async function handleDragEnd(event){
        const {active, over} = event;
        const activeIndex = questionsData.findIndex((question) => question.id === active.id)
        const overIndex = questionsData.findIndex((question) => question.id === over.id)
        let order = arrayMove(questionsData, activeIndex, overIndex);
        await setQuestionsData([])
        await setQuestionsData(order);          
    }

    return(
        <DndContext
            collisionDetection={closestCenter}
            onDragEnd={handleDragEnd}
        >
            <ViewHeader detail={true} name={name} content={"Pregunta"}
            createMethod={addQuestion} disabled={!enabled || reorder} reorder={reorder}
            handleReorder={changeMode}/> 
        
            <div className="questionContainer">
                <SortableContext
                    items={questionsData}
                    strategy={verticalListSortingStrategy}
                >                   
                    {questionsData.map((questions, index) => (
                        <QuestionContainer number={index} key={index} id={questions.id}
                        index={index} updateData={changeQuestionData} data={questions}
                        reorder={reorder}/>                                                            
                    ))}
                </SortableContext>
            </div>            
                                                                                                                
            <div style={{padding: "1vh"}}></div>
            <Button variant="primary" style={{"marginLeft": "85%"}} onClick={saveChanges}
                disabled={!enabled}>Guardar Cambios</Button>
            <div style={{padding: "1vh"}}></div>
        </DndContext>         
    )
}
