import { Form } from "react-bootstrap";

export default function TrueFalseResponse(props){

    return(
        <>
          <Form.Group>
          <Form.Label>Escoja respuesta correcta</Form.Label>
          <div>
            <Form.Check
              type="radio"
              name={"answerTrueOrFalse" + props.number}
              value="true"
              label="Verdadero"
              disabled={props.reorder}
              id={"radio" + props.number}
              checked={props.selected === 'true'}
              onChange={props.handleRadioOptionChange}
            />
            <Form.Check
              type="radio"
              name={"answerTrueOrFalse" + props.number}
              value="false"
              label="Falso"
              disabled={props.reorder}
              id={"radio" + props.number}
              checked={props.selected === 'false'}
              onChange={props.handleRadioOptionChange}
            />
              </div>
          </Form.Group>
        </>
    )
}