import { useEffect, useState } from "react";
import ViewHeader from "../Header/ViewHeader";
import ContentTable from "../Common/Table";
import Options from "../Common/Options";
import Valid from "../Common/Valid";
import CreateModal from "../Modals/CreateModal";
import UpdateModal from "../Modals/UpdateModal";
import DeleteModal from "../Modals/DeleteModal";
import NotifyModal from "../Modals/NotifyModal";
import { Spinner } from "react-bootstrap";

import moment from 'moment';
import 'moment-timezone';

export default function Evaluation(){

    const columns = [
        "Nombre", "Fecha de Creación", "Fecha de Termino", "Activa", "Opciones"
    ]
    const [data, setData] = useState([])

    const [showCreate, setShowCreate] = useState(false)
    const [showUpdate, setShowUpdate] = useState(false)
    const [showDelete, setShowDelete] = useState(false)
    const [showNotify, setShowNotify] = useState(false)

    const [selectedId, setSelectedId] = useState('0')
    const [loaded, setLoaded] = useState(false)

    function showUpdateModal(selectId){
        setSelectedId(selectId)
        setShowUpdate(true)
    }

    function showDeleteModal(selectId){
        setSelectedId(selectId)
        setShowDelete(true)
    }

    function showNotifyModal(selectId){
        setSelectedId(selectId)
        setShowNotify(true)
    }

    function getAllEvaluations(){
        setData([])
        const uToken = localStorage.getItem("token")
        let dataUrl = `${process.env.REACT_APP_BACK_URL}/evaluation`
        fetch(dataUrl, {method: "GET", headers: {'Authorization' : "Bearer "+ uToken}})
        .then(response => response.json())
        .then(res => {
            let evaluations = res.data
            let evaluationsData = []
            evaluations.forEach((eIndex) => {
                let offset = moment().tz("America/Santiago").utcOffset()/60
                let evaluationIndexData = [
                    eIndex.name,
                    moment(eIndex.startTime, "DD/MM/YYYY HH:mm:ss").add(offset, "hours").format("DD/MM/YYYY HH:mm:ss"),
                    moment(eIndex.endTime, "DD/MM/YYYY HH:mm:ss").add(offset, "hours").format("DD/MM/YYYY HH:mm:ss"),
                    <Valid valid={eIndex.active}/>,
                    <Options send={true} id={eIndex.id} showNotify={showNotifyModal}
                    showUpdate={showUpdateModal} showDelete={showDeleteModal}/>
                ]
                evaluationsData.push(evaluationIndexData)
            })
            setData(evaluationsData)
            setLoaded(true)
        })
    }

    useEffect(() => {
        getAllEvaluations()
    }, [])

    return(
        <>
            {loaded ? 
            <>
                <CreateModal show={showCreate} model={"evaluación"}
                         handleClose={() => {setShowCreate(false)}} submit={getAllEvaluations}/>
                {selectedId !== "0" ? 
                    <UpdateModal show={showUpdate} model={"evaluación"} id={selectedId}
                        handleClose={() => {setSelectedId("0"); setShowUpdate(false)}} submit={getAllEvaluations}/> 
                : <></>}  
                <DeleteModal show={showDelete} model={"evaluación"} id={selectedId}
                            handleClose={() => {setShowDelete(false)}} submit={getAllEvaluations}/>
                <NotifyModal show={showNotify} model={"evaluación"} id={selectedId}
                            handleClose={() => {setShowNotify(false)}} submit={getAllEvaluations}/>
                <ViewHeader content={"Evaluaciones"} createMethod={() => {setShowCreate(true)}}/> 
                <ContentTable columns={columns} data={data}></ContentTable>  
            </>
            : <div style={{display: "flex", alignItems: "center", justifyContent: "center"}}><Spinner/></div>}
            
        </>
    )
}